/*//////////////////////////////////
Styling for image gallery library 
//////////////////////////////////*/

/* CSS */
@import "~react-image-gallery/styles/css/image-gallery.css";

/*/////////////////////
//General Tag's Styling 
/////////////////////*/
* {
  font-family: 'Roboto', sans-serif;
  color: white;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Bebas Neue', cursive; 
  letter-spacing: 2px;
}

header {
  position: relative;
  z-index: 1000;
}

a {
  text-decoration: none;
}

u {
  text-decoration: underline;
  display: inline;
}

b {
  font-weight: 900;
  display: inline;
}


/*////////////////////////////
General styling for all pages
////////////////////////////*/
#content-container {
  height: 100vh;
  background-color: black;
  z-index: -3;
}

.content-container-alt {
  height: 100vh;
  z-index: -3;
}

.width-size {
  padding: 0 0px;
  width: 1180px;
  margin: 0 auto;
}

.bottom-pad-med {
  padding-bottom: 8vh;
}

.center {
  text-align: center;
}

#copyright {
  letter-spacing: 2px;
  width: 1180px;
  margin: 0 auto;
  font-size: 12px;
  padding-bottom: 24px;
  text-align: center;
  color: lightgray
}

@media (max-width: 648px) {
  .width-size { width: 100% }
  #copyright { width: 100%; margin-top: 24px;}
}

/*//////////////////////////
General styling for icons
//////////////////////////*/
.scroll-down-icon {
  margin-top: 16px;
  font-size: 40px;
  color: white;
  transition: 0.3s;
  cursor: pointer;
}
  .scroll-down-icon:hover {
    opacity: 0.4;
  }

/*/////////
Animations 
//////////*/
.fade-in-2 {
  animation: fadeIn ease 2s;
  -webkit-animation: fadeIn ease 2s;
  -moz-animation: fadeIn ease 2s;
  -o-animation: fadeIn ease 2s;
  -ms-animation: fadeIn ease 2s;
}

.fade-in-3 {
  animation: fadeIn ease 3s;
  -webkit-animation: fadeIn ease 3s;
  -moz-animation: fadeIn ease 3s;
  -o-animation: fadeIn ease 3s;
  -ms-animation: fadeIn ease 3s;
}

.fade-in-4 {
  animation: fadeIn ease 4s;
  -webkit-animation: fadeIn ease 4s;
  -moz-animation: fadeIn ease 4s;
  -o-animation: fadeIn ease 4s;
  -ms-animation: fadeIn ease 4s;
}


.fade-in-6 {
  animation: fadeIn ease 6s;
  -webkit-animation: fadeIn ease 6s;
  -moz-animation: fadeIn ease 6s;
  -o-animation: fadeIn ease 6s;
  -ms-animation: fadeIn ease 6s;
}

@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

.float {
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0% {
      transform: translatey(0px);
      box-shadow: 0 5px 15px 0px rgba(255,255,255,0.6);
  }
  50% {
      
  box-shadow: 0 25px 15px 0px rgba(255,255,255,0.2);
      transform: translatey(-20px);
  }
  100% {
      box-shadow: 0 5px 15px 0px rgba(255,255,255,0.6);
      transform: translatey(0px);
  }
}

/* Zoom animation */
.animate {
  -webkit-animation: animatezoom 0.8s;
  animation: animatezoom 0.4s
}

@-webkit-keyframes animatezoom {
  from {-webkit-transform: scale(0)}
  to {-webkit-transform: scale(1)}
}

@keyframes animatezoom {
  from {transform: scale(0)}
  to {transform: scale(1)}
}
/*//////////////////////
Navigation bar styling 
//////////////////////*/
#portfolio-logo {
  margin-top: 19px;
  margin-right: 4px;
  width: 80px;
  height: auto;
  vertical-align: middle;
}

#navbar-container {
  padding: 54px 41.5px 0 72px;
  z-index: -5;
}

#navbar-contents {
  font-size: 36px;
  font-family: 'Bebas Neue', cursive; 
}

  #navbar-contents * { text-decoration: none;}
  #navbar-contents > * {
    letter-spacing: 1px;
    margin-right: 48px;
    transition: 0.3s;
    cursor: pointer;
    display: inline;
  }

  #navbar-contents > *:last-of-type {
    margin-right: 0;
  }

  #navbar-contents > *:hover {
    opacity: 0.5;
  }

#logo {
  vertical-align: middle;
  border-radius: 50%;
  display: inline-block;
  width: 100px;
  height: 100px;
  background-color: white;
  font-size: 60px;
  margin: 0 48px;
  z-index: -6;
}


/*Mobile Responsive Design */
#navbar-menu-button {
  z-index: 10;
  display: none;
  position: absolute;
  left: 24px;
  top: 24px;
  height: 24px;
  width: auto;
}

#navbar-menu-content {
  display: flex;
  position: fixed;
  width: 80vw;
  height: 100vh;
  top: 0;
  left: -80%;
  opacity: 0;
  background: #101010;
  z-index: 7;
  padding: 100px 0 0 24px;
  text-align: left;
  transition: all 1.2s ease;
  flex-direction: column;
}

#navbar-menu-content > ul {
  font-size: 20px;
  font-family: 'Bebas Neue';
  margin: 16px 0;
  letter-spacing: 2px;
}
#navbar-menu-content > hr {
  width: calc(100% - 24px);
  margin: 0;
  color:gray;
  background: gray;
  border-color: rgba(255,255,255,0.25)
}

@media (max-width: 648px) {
  #navbar-container {
    padding: 0;
    margin-top: 16px;
    position: relative;
    height: 72px;
    width: 100%;
  }
  #navbar-contents > * { display: none; margin: 0}
  #navbar-contents > *:nth-of-type(3) { display: block; }
  #logo { width: 72px; height: 72px;}
  #portfolio-logo {width: 56px; margin-top: 14px;}

  #navbar-menu-button { display: inline; }
  #navbar-menu-content.active {
    left: 0;
    opacity: 1;
    transition: all 1s ease; 
  }
}

/*//////////// 
Random styling
////////////*/
.invisible {
  display: none;
}

.black-overlay {
  height: 100%;
  width: 100%;
  background-color: rgba(255,255,255,0.3);
}

.white-overlay {  
  position: fixed;
  top: 0;
  left: 0;
  background: white;
  opacity: 0.56;
  width: 100%;
  height: 100%;
  z-index: 5
}

.no-scroll {
  height: 100%;
  overflow: hidden;
}